#home {
    max-width: 90%;
    width: 600px;
    margin-left: auto;
    margin-right: auto;
    padding-top: 20vh;
    section {
        padding-bottom: 50px;
    }
    @extend .editor_content;
    #intro {
        position: relative;
        ul#social {
            position: absolute;
            top: 17px;
            right: 0;
            padding: 0;
            margin: 0;
            list-style: none;
            @include media-breakpoint-down(md) {
                top: -3px;
            }
            @media (max-width:374px) {
                top: -30px;
            }
            li {
                display: inline-block;
                font-size: 24px;
                margin-right: 20px;
                margin-bottom: 0;
                a {
                    display: inline;
                    &:before {
                        display: none;
                    }
                }
            }
        }
    }
    #projects-list {
        margin-top: 20px;
        display: none;
        overflow: hidden;
        &.slick-initialized {
            display: block;
        }
        .slick-list {
            overflow: unset;
        }
        .slick-arrow {
            position: absolute;
            color: $cyan;
            cursor: pointer;
            font-size: 20px;
            top: 3px;
            &:hover {
                color: $cyanBlue;
            }
        }
        .prev {
            right: calc(100% + 40px);
        }
        .next {
            right: calc(100% + 15px);
        }
        @include media-breakpoint-down(md) {
            .next {
                right: 0;
            }
            .prev {
                right: 25px;
            }
        }
    }
    .slick-slide {
        &:focus {
            outline: none;
        }
        margin-right: 0px;
        @include media-breakpoint-down(sm) {
            margin-right: 0;
        }
    }
    .project {
        .project-inner {
            position: relative;
            padding-right: 30%;
            h4 {
                font-size: 24px;
            }
            img {
                position: absolute;
                top: 50%;
                right: 5%;
                width: 20%;
                height: auto;
                transform: translateY(-50%);
                overflow: hidden;
                border-radius: 5px;
                box-shadow: 0 3px 20px rgba(0, 0, 0, .3);
            }
            @include media-breakpoint-down(sm) {
                padding-right: 0;
                img {
                    margin-top: 20px;
                    margin-bottom: 30px;
                    position: static;
                    top: 0;
                    width: 80%;
                    margin-left: auto;
                    margin-right: auto;
                    transform: none;
                }
            }
        }
        .project-link {
            a {
                font-size: 20px;
                @include gradient-underline;
            }
        }
    }


    #contact-heading a{
        @include gradient-underline;
        display: inline-block;
        padding-bottom: 3px;
    }
    #contact{
        .fields,.thanks{
            display: none;
        }
    }
}