@mixin ul-arrow{
    list-style: none;
    padding-left: 0;
    li{
        padding-left: 30px;
        position: relative;
        &:before{
            content: "\f061";
            position: absolute;
            @include fa(r);
            left: 0;
        }
    }
}
.ttu{
    text-transform: uppercase;
}
.editor_content {

    .dots{
        position: relative;
        &:before{
            display: block;
            content: "\2022";
            position: absolute;
            right: 101.5%;
            transform: scale(1.8);
        }
    }
    a{
        color: $green;
        position: relative;
        display: inline-block;
        &:before{
            display: block;
            content: " ";
            position: absolute;
            bottom: 0;
            width: 0;
            height: 2px;
            background: $cyan;
            // transform: translateY(-5px);
            opacity: 0;
            @include anim;
        }
        &:hover{
            text-decoration: none;
            &:before{
                width: 100%;
                // transform: translateY(0);
                opacity: 1;
            }
        }
    }
    h1, .h2, h2, .h2, h3, .h3, h4, .h4, h5, .h5, h6, .h6{
        margin-top: 0;
        margin-bottom: 10px;
    }
    h1, .h1 {
        font-size: 50px;
        line-height: 58px;
        @include media-breakpoint-down(md){
            font-size: 28px;
            line-height: 34px;
        }
    }

    h2, .h2 {
        font-size: 35px;
        line-height: 45px;
        @include media-breakpoint-down(md){
            font-size: 22px;
            line-height: 26px;
        }
    }

    h3, .h3{
        font-size: 24px;
        line-height: 30px;
        @include media-breakpoint-down(md){
            font-size: 20px;
            line-height: 24px;
        }
    }

    h4, .h4, h5, .h5, h6, .h6 {
        font-size: 20px;
        line-height: 26px;
        @include media-breakpoint-down(md){
            font-size: 18px;
            line-height: 22px;
        }
    }

    p {
        margin: 0 0 18px 0;
        &:last-child{
            margin-bottom: 0;
        }

    }
    .intro,.ingress{
        font-weight: 600;
        font-size: 24px;
        line-height: 34px;
        @include media-breakpoint-down(md){
            font-size: 18px;
            line-height: 22px;
        }
    }

    ul, ol {
        margin-top: 0;
        margin-bottom: 10px;
        padding-left: 30px;
        li{
            padding-bottom: 10px;
        }
        &:last-child{
            margin-bottom: 0;
        }
    }

    ul.arrow{
        @include ul-arrow;
    }


    ol{
        padding-left: 20px;
        li{
            padding-left: 10px;
        }
    }

    blockquote, q {}

    > *:last-child {
        margin-bottom: 0;
    }

    

    blockquote,.important,.marked{
        border-left: 2px solid $green;
        margin-left: 77px;
        padding-left: 18px;
        @include media-breakpoint-down(md){
            margin-left: 25px;
        }
        @include media-breakpoint-down(sm){
            margin-left: 0;
        }
    }


}

.content-fact-box{
    background: $green;
    color: #fff;
    padding: 50px 100px;
    margin-bottom: 30px;
    @include media-breakpoint-down(md){
        padding: 25px 50px;
    }
    @include media-breakpoint-down(sm){
        padding: 30px 0px 20px;
        position: relative;
        z-index: 10;
        &:before{
            content: " ";
            display: block;
            background: $green;
            width: 100%;
            height: 100%;
            position: absolute;
            transform: scaleX(1.3);
            left:0;
            top:0;
            z-index: -1;
        }
    }
    strong{
        font-weight: 600;
    }
    h1,h2,h3,h4,h5,h6{

        &.cfb-title,&.a-title{
            font-size: 18px;
            line-height: 21px;
            margin-bottom: 20px;
            font-weight: 600;
            text-transform: uppercase;
        }
    }
    
}


section.block{

    &:last-of-type{
        margin-bottom: 150px;
        @include media-breakpoint-down(md){
            margin-bottom: 70px;
        }
    }
}