
@keyframes shake {
  10%, 90% {
    transform: translate3d(-1px, 0, 0);
  }
  
  20%, 80% {
    transform: translate3d(2px, 0, 0);
  }

  30%, 50%, 70% {
    transform: translate3d(-4px, 0, 0);
  }

  40%, 60% {
    transform: translate3d(4px, 0, 0);
  }
}


@keyframes backgroundGradientAnimate {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}


@mixin gradient-underline{
	display: inline-block;
	position: relative;
	text-decoration: none;
	color: inherit;
	z-index: 1;
	&:before{
		content: '';
		position: absolute;
		left: 0;
		bottom: 0;
		height: 4px;
		width: 0;
		background: linear-gradient(110deg, #e1f549, #29d0be, #6cb8ea, #ff5959);
		background-size: 300% 300%;
		z-index: -1;
		transition: width .5s cubic-bezier(.6,0,.4,1);

		-webkit-animation: backgroundGradientAnimate 3s ease infinite;
		-moz-animation: backgroundGradientAnimate 3s ease infinite;
		animation: backgroundGradientAnimate 3s ease infinite;
	}
	&:hover{
		&:before{
			width: 100%;
		}
	}
}

@mixin fa($w: false){
	font-family: 'Font Awesome 5 Pro';

	@if $w == l{
		font-weight: 300;
	} @else if $w == s {
		font-weight: 900;
	} @else if $w == r {
		font-weight: 400;
	} @else {
		font-weight: 900;
	}
}

@mixin anim{
	transition: all 400ms ease;
}


@mixin opacity($opacity) {
	opacity: $opacity;
	$opacity-ie: $opacity * 100;
	filter: alpha(opacity=$opacity-ie);
}

// Clearfix
%clearfix {
	&:after {
		content: '';
		display: table;
		clear: both;
		width: 100%;
	}
}
	
// Input styles
@mixin input-placeholder {
	&.placeholder { @content; }
	&:-moz-placeholder { @content; }
	&::-moz-placeholder { @content; }
	&:-ms-input-placeholder { @content; }
	&::-webkit-input-placeholder { @content; }
}
	
// Fallback for svg
@mixin svg-background-fallback($image-url, $svg-url) {
	background: url($image-url);
	background: url($svg-url), linear-gradient(transparent, transparent);
}

// Media queries
@mixin media-breakpoint-up($breakpoint) { 
	@if map-has-key($grid-breakpoints, $breakpoint) {
		@media (min-width: #{map-get($grid-breakpoints, $breakpoint)+1}) {
			@content;
		}
	}
	@else {
		@warn "Unfortunately, no value could be retrieved from `#{$breakpoint}`. "
			+ "Please make sure it is defined in `$breakpoints` map.";
	}
}

@mixin media-breakpoint-down($breakpoint) { 
	@if map-has-key($grid-breakpoints, $breakpoint) {
		@media (max-width: #{map-get($grid-breakpoints, $breakpoint)}) {
			@content;
		}
	}
	@else {
		@warn "Unfortunately, no value could be retrieved from `#{$breakpoint}`. "
			+ "Please make sure it is defined in `$breakpoints` map.";
	}
}